<script setup lang="ts">
import { ref } from 'vue'

const currentStep = ref(1)

const formData = ref({
  insuranceType: '',
  fullName: '',
  email: '',
  phone: '',
})

const nextStep = () => {
  if (currentStep.value < 3) currentStep.value++
}
const prevStep = () => {
  if (currentStep.value > 1) currentStep.value--
}
</script>

