        />
        <input type="hidden" name="_next" value="/thank-you" />
        <input type="hidden" name="form_type" value="quote" />

        <!-- Step 1 -->
        <div v-show="currentStep === 1" class="space-y-6">
          <h2 class="text-xl font-semibold">I am seeking coverage for</h2>

          <div class="space-y-2">
            <label for="insuranceType" class="text-sm font-medium">Insurance Type</label>
            <select
